@import "../../../_mixin/mixin";
@import "../../../_variable/variable";


.logotype {
	cursor: pointer;
	position: absolute;
	z-index: 15;
	left: 0;
	right: 0;
	top: 25px;
	margin: {
		left: auto;
		right: auto;
	};
	@include respond-to(small-max) {
		.footer & {
			position: static;
		}
		.menu & {
			position: static;
			display: block;
			padding: {
				top: 25px;
			};
			margin: {
				bottom: 25px;
			}
		}
	}
}